.container{
    width: 50%;
    margin: 0 auto;
    padding-top: 140px;
  }
  @keyframes load{
    from {
      width: 0%
    }
  }
  @-webkit-keyframes load{
    from {
      width: 0%
    }
  }
  
  .bar{
    background-color: #EEE;
    padding: 2px;
    border-radius: 15px;
    margin-bottom: 5px;
    font-size: 14px;
    color: #FFF;
    font-weight: bold;
    text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  }
  .bar::before{
    content:  attr(data-skill);
    background-color: darksalmon;
    display: inline-block;
    padding: 5px 0 5px 10px;
    border-radius: inherit;
    animation: load 2s 0s;
    -webkit-animation: load 2s 0s;
    -moz-animation: load 2s 0s;
    -o-animation: load 2s 0s;
  }
  
  .bar.front::before{
    background-color: darksalmon;
  }
  .bar.back::before{
    background-color: black;
  }
  
  .bar.learning::before{
    width: calc(20% - 10px);
  }
  .bar.basic::before{
    width: calc(40% - 10px);
  }
  .bar.intermediate::before{
    width: calc(60% - 10px);
  }
  .bar.advanced::before{
    width: calc(80% - 10px);
  }
  .bar.expert::before{
    width: calc(100% - 10px);
  }
  
.Sidenav{
  height:100%;
  width: 100px;
  position: fixed;
  z-index: 0;
  top:0;
  left:0;
  background-color: darksalmon;
  box-shadow: 0 2px 4px 0 rgba(0,0, 0.2);
  overflow: hidden;
  padding-top: 20px;
}
.Sidenav li{
  padding: 46px 48px 8px 30px;
  text-decoration: none;
  list-style: none;
  display: block;
  color: black;
  font-size: 35px;
}
.Sidenav li a{
  text-decoration: none;
color: black;
font-weight: 700;

}
.Sidenav li a:hover{
color:white;
}
